import React, {useMemo, useRef, useState} from "react";
import {Box, Flex} from "@react-three/flex";
import Human from "../../components/Human";
import {Text} from "@react-three/drei";
import {useFrame, useLoader, useThree} from "@react-three/fiber";
import {SVGLoader} from "three-stdlib";
import * as THREE from "three";

function Loading() {
    return (
        <Text>Loading...</Text>
    )
}

function Triangle({ color, ...props }) {
    const ref = useRef()
    const [r] = useState(() => Math.random() * 10000)
    useFrame((_) => (ref.current.position.y = -1.75 + Math.sin(_.clock.elapsedTime + r) / 10))
    const { paths: [path] } = useLoader(SVGLoader, '/img/triangle.svg') // prettier-ignore
    const geom = useMemo(() => SVGLoader.pointsToStroke(path.subPaths[0].getPoints(), path.userData.style), [])
    return (
        <group ref={ref}>
            <mesh geometry={geom} {...props}>
                <meshBasicMaterial color={color} toneMapped={false} />
            </mesh>
        </group>
    )
}

function Rig({ children }) {
    const ref = useRef()
    const vec = new THREE.Vector3()
    const { camera, mouse } = useThree()
    useFrame(() => {
        camera.position.lerp(vec.set(mouse.x * 2, 0, 3.5), 0.05)
        ref.current.position.lerp(vec.set(mouse.x * 1, mouse.y * 0.1, 0), 0.1)
        ref.current.rotation.y = THREE.MathUtils.lerp(ref.current.rotation.y, (-mouse.x * Math.PI) / 20, 0.1)
    })
    return <group ref={ref}>{children}</group>
}

const Hero = () => {
    const { viewport } = useThree();
    return (
        <>
            {/*<Flex size={[viewport.width, viewport.height, 0]}*/}
            {/*      position={[-viewport.width / 2, viewport.height / 2, 0]}*/}
            {/*      justifyContent="center"*/}
            {/*      alignItems="center"*/}
            {/*      plane="xy">*/}
            {/*    <Box height="auto">*/}
            {/*        <React.Suspense fallback={<Loading />}>*/}
            {/*            <Human position={[-1.2,-1.5,-5]} />*/}
            {/*        </React.Suspense>*/}
            {/*    </Box>*/}
            {/*</Flex>*/}
            <React.Suspense fallback={<Loading />}>
                <Rig>
                    <Triangle color="#ff2060" scale={0.009} rotation={[0, 0, Math.PI / 3]} />
                    <Triangle color="cyan" scale={0.01} position={[2, 0, -2]} rotation={[0, 0, Math.PI / 3]} />
                    <Triangle color="orange" scale={0.1} position={[-2, 0, -2]} rotation={[0, 0, Math.PI / 3]} />
                    <Triangle color="white" scale={1} position={[0, 2, -10]} rotation={[0, 0, Math.PI / 3]} />
                </Rig>
            </React.Suspense>
        </>
    )
}

export default Hero;