import React, {useState} from "react";
import HeroKyc from "./HeroKyc";
import ServicesKyc from "./ServicesKyc";
import SolveKyc from "./SolveKyc";
import KYCNavigation from "../../components/elements/KYCNavigation";
import ExpectKyc from "./ExcpectKyc";
import WhatIsKyc from "./WhatIsKyc";

const KYC = ({nextPage}) => {
    const [state, setState] = useState('Hero');

    return (
        <>
            <div>
                <div className="navigation-scrollbar-kyc grid grid-flow-col auto-cols-auto">
                    <KYCNavigation title={'What is KYC'} getState={state} addTrip={setState}/>
                    <KYCNavigation title={'What risks you can reduce?'} getState={state} addTrip={setState}/>
                    <KYCNavigation title={'Services'} getState={state} addTrip={setState}/>
                    <KYCNavigation title={'You are going to receive'} getState={state} addTrip={setState}/>
                </div>
            </div>
            {state === 'Hero' &&
            <div className="container mx-auto">
                <HeroKyc nextPage={() => setState('What is KYC')}/>
            </div>
            }
            {state === 'What is KYC' &&
            <div className="container mx-auto px-8 sm:px-28 md:px-0">
                <WhatIsKyc nextPage={() => setState('What risks you can reduce?')}/>
            </div>
            }
            {state === 'What risks you can reduce?' &&
            <div className="container mx-auto px-8 sm:px-28 md:px-0">
                <SolveKyc nextPage={() => setState('Services')}/>
            </div>
            }
            {state === 'Services' &&
            <div className="container mx-auto px-8 sm:px-28 md:px-0">
                <ServicesKyc nextPage={() => setState('You are going to receive')}/>
            </div>
            }
            {state === 'You are going to receive' &&
            <div className="container mx-auto px-8 sm:px-28 md:px-0">
                <ExpectKyc nextPage={() => setState('Hero')}/>
            </div>
            }
        </>
    )
}
export default KYC;
