import React from "react";
import {Canvas} from "@react-three/fiber";
import Lights from "../../components/Lights";
import {OrbitControls} from "@react-three/drei";
import Hero from "./Hero";



const WeAreBlock = ({nextPage}) => {
    return (
        <div id="we-are">
            <div className="grid lg:grid-cols-12">
                <div className="col-span-6 flex justify-center items-center lg:items-end flex-col py-12 lg:py-0">
                    <div className="absolute" style={{ fontSize: "" }}>02</div>
                    <div className="flex flex-col justify-start relative text-center sm:text-left w-full sm:w-3/4 lg:w-auto">
                        <div className="css--we-are-decor" style={{zIndex: 1}}>
                            <div className="css--we-are-decor-inner">
                                <img src="/img/discover_expand/rectangle_inner.svg" alt="" />
                            </div>
                        </div>
                        <div className="css--we-are-header-first" style={{zIndex: 2}}>we are</div>
                        <div className="css--we-are-header-second" style={{zIndex: 2}}>cyborgs</div>
                    </div>
                    <div className="w-3/4 mt-8">Our creative/physical abilities are extended beyond normal human limitations by mechanical elements built into the body/mind.</div>
                    <div className="w-3/4 mt-14">
                        <button className="btn" onClick={nextPage}>How?</button>
                    </div>
                </div>
                <div className="col-span-6 h-screen css--we-are-hero">
                    <div style={{zIndex: -1, width: "100%", height: "100%", position: "absolute"}}>
                        <Canvas
                            // shadows
                            // dpr={[1, 1.5]}
                            camera={{position: [0, -1, 15], near: 0.001}}>
                            <Lights/>
                            <OrbitControls/>
                            <Hero/>
                        </Canvas>
                    </div>
                    <div className="absolute css--we-are-description p-8 grid grid-cols-2">
                        <div>
                            <div className="mb-4 text-white font-bold">Technology</div>
                            <div>Lorem ipsum dolor sit amet, consectetur adipiscing elit</div>
                        </div>
                        <div>
                            <div className="mb-4 text-white font-bold">Innovation</div>
                            <div>Lorem ipsum dolor sit amet, consectetur adipiscing elit</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default WeAreBlock;
