import React from "react";
import {graphql, useStaticQuery} from "gatsby";

export default function Projects() {
    const data = useStaticQuery(graphql`
    query {
      allWpPost(
        sort: {fields: [date], order: DESC}
        filter: {categories: {nodes: {elemMatch: {name: {eq: "projects"}}}}}
      ) {
          nodes {
            id
            title
            slug
            excerpt
            date(formatString: "YYYY/MM/DD")
            tags {
              nodes {
                name
              }
            }
            featuredImage {
              node {
                sourceUrl
              }
            }
          }
      }
    }
  `)
    return (
        <div id="projects" className="pt-14 relative">
            <div className="container px-4 mx-auto">
                <div className="text-5xl glitch text-center" data-glitch="Success stories">
                    Success stories
                </div>
                <div className="grid lg:grid-cols-2 md:grid-cols-1 gap-4 relative pb-28">
                    {data.allWpPost.nodes.map(node => (
                        <div className="">
                            <div className="mb-32 flex justify-center items-center" key={node.slug}>
                                <div className="max-w-lg rounded css--projects-bg">
                                    <div className="css--projects-photo">
                                        <img className="object-cover h-full w-full" src={node.featuredImage.node.sourceUrl} alt="Project image"/>
                                    </div>
                                    <div className="px-6 pt-4 pb-2 flex flex-row">
                                        {node.tags.nodes.map(tag => (
                                            <span className="text-center mr-5">
                                                <img src={`/img/develop_stack/${tag.name}.svg`} style={{height: "40px", margin: "10px auto"}} alt={tag.name} title={tag.name}/>
                                                {tag.name}
                                            </span>
                                        ))}
                                    </div>
                                    <div className="px-6 py-4">
                                        <div className="font-bold text-white text-xl mb-2">{node.title}</div>
                                        <p className="text-white text-base" dangerouslySetInnerHTML={{__html: node.excerpt}}/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>

            </div>
        </div>
    )
}