import * as React from "react"

import "../style/global.scss";
import LetsTalk from "../layout/LetsTalk";
import Projects from "../layout/Projects";
import HeroBlock from "../layout/HeroBlock";
import AddTripButton from "../components/elements/condButton";
import WeAreBlock from "../layout/WeAre";
import FadedText from "../components/helpers/FadedText";
import KYC from "../layout/KYC";
import anime from "animejs";
import TextScramble from "../components/helpers/TextScramble";
import Loader from "../components/elements/Loader";
import Navigation from "../components/elements/Navigation";

class IndexPage extends React.Component {
    constructor(props) {
        super(props);
        require("../style/hero_block.scss");
        require("../style/we_are_block.scss");
        require("../style/kyc_block.scss");
        this.state = {
            data: '1',
            page: '01'
        }

        function getRandomInt(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min + 1)) + min;
        }

        let elements = document.getElementsByClassName('text-scramble');
        Array.prototype.forEach.call(elements, function (el) {
            let glitch_text = new TextScramble(el)
            const next_update = () => {
                glitch_text.setText().then(() => {
                    setTimeout(next_update, 13000 + getRandomInt(0, 5000))
                })
            }
            next_update();
        });
    }

    componentDidMount() {
        let timeline = anime({
            easing: 'easeOutExpo',
            autoplay: true,
            targets: '#main-wrapper section',
            opacity: [0, 1],
            perspective: 200,
            duration: 1500,
            delay: 1000
        });
    }

    render() {
        return (
            <main>
                <title>/KALMARS/ - WEB Development | KYC/AML | Or anything complex</title>
                {/*<div id="loader" className="fixed w-full h-full flex justify-center items-center" style={{ left: 0, top: 0 }}>*/}
                {/*    <Loader />*/}
                {/*</div>*/}
                <div id="main-wrapper">
                    <Navigation one={() => this.setState({page: '01'})} two={() => this.setState({page: '02'})}/>

                    <div>
                        <div className="navigation-scrollbar">
                            <AddTripButton title={'01'} getState={this.state.page} nextPage={() => this.setState({page: '01'})}/>
                            <AddTripButton title={'02'} getState={this.state.page} nextPage={() => this.setState({page: '02'})}/>
                            <AddTripButton title={'03'} getState={this.state.page} nextPage={() => this.setState({page: '03'})}/>
                            <AddTripButton title={'04'} getState={this.state.page} nextPage={() => this.setState({page: '04'})}/>
                            <AddTripButton title={'05'} getState={this.state.page} nextPage={() => this.setState({page: '05'})}/>
                        </div>
                    </div>
                    <div style={{position: "fixed", bottom: "10px", left: "0", zIndex: "50"}} className="grid grid-cols-12 w-full justify-between">
                        <div className="col-span-12 sm:col-span-3 flex justify-center">
                            <img src="/img/logo/logo_text_White-full.svg" style={{height: "50px", opacity: 0.2}} className="mx-4" alt="Kalmars Logo"/>
                        </div>
                        <div className="col-span-12 sm:col-span-6 flex justify-center items-center">
                            <a href="https://goo.gl/maps/QWNKNHRk13UjGTo7A" target="_blank" rel="noreferrer" className="text-secondary text-scramble">i 56.9282618N  24.006794W</a>
                        </div>
                        <div className="col-span-12 sm:col-span-3 flex justify-center items-center">
                            info@kalmars.lv
                        </div>
                    </div>

                    {this.state.page === '01' &&
                    <section>
                        <HeroBlock nextPage={() => this.setState({page: '02'})}/>
                    </section>
                    }
                    {this.state.page === '02' &&
                    <section>
                        <FadedText text={'DISCOVER'} direction="left"/>
                        <WeAreBlock nextPage={() => this.setState({page: '03'})}/>
                    </section>
                    }
                    {this.state.page === '03' &&
                    <section>
                        <FadedText text={'EXPAND'} direction="left"/>
                        <KYC/>
                    </section>
                    }
                    {this.state.page === '04' &&
                    <section>
                        <FadedText text={'DISCOVER'} direction="left"/>
                        <Projects/>
                    </section>
                    }
                    {this.state.page === '05' &&
                    <section>
                        <FadedText text={'EXPAND'} direction="left"/>
                        <LetsTalk/>
                    </section>
                    }
                </div>
            </main>
        )
    }
}

export default IndexPage
