import React, {useEffect, useRef} from 'react'
import {useGLTF, useAnimations, Billboard, Text} from '@react-three/drei'

export default function Model(props) {
    const group = useRef()
    const {nodes, materials, animations} = useGLTF('/models/human.glb')
    const {actions} = useAnimations(animations, group)
    useEffect(() => {
        actions.long_idle.play();
    })
    return (
        <group ref={group} {...props} dispose={null}>
            <Billboard follow={true} position={[0, 3, 1]}>
                <Text fontSize={0.1} >Access key</Text>
            </Billboard>
            <group rotation={[Math.PI / 2, 0, 0.5]} scale={0.02}>
                <primitive object={nodes.mixamorigHips}/>
                <skinnedMesh
                    geometry={nodes.Mesh002.geometry}
                    material={materials['Ch44_Body.002']}
                    skeleton={nodes.Mesh002.skeleton}
                />
                <skinnedMesh
                    geometry={nodes.Mesh002_1.geometry}
                    material={materials['Ch44_body1.002']}
                    skeleton={nodes.Mesh002_1.skeleton}
                />

            </group>
        </group>
    )
}

useGLTF.preload('/models/human.glb')
